.preview-container {
  /* 移除背景色 */
  border-radius: 20px;

  padding-left: 20px;
  padding-right: 20px;
  max-width: 410px;
  margin: 0 auto;
  /* 移除阴影 */
}

.phone-frame {
  background-color: #1a1a1a;
  border-radius: 50px;
  padding: 15px;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.3);
  position: relative;
}

.phone-screen {
  background-color: white;
  border-radius: 40px;
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 680px;
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.1);
}

.phone-status-bar {
  background-color: white;
  height: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px;
  font-size: 12px;
  color: #333;
}

.phone-content {
  padding: 20px;
  height: calc(100% - 65px);
  overflow-y: auto;
}

.app-logo {
  width: 80px;
  height: 80px;
  margin: 20px auto;
  display: block;
}

.app-title {
  text-align: center;
  font-size: 18px;
  margin-bottom: 30px;
}

.login-form input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.login-form button {
  width: 100%;
  padding: 10px;
  background-color: #00c1b2;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
}

.phone-home-indicator {
  position: absolute;
  bottom: 5px;
  left: 50%;
  transform: translateX(-50%);
  width: 120px;
  height: 5px;
  background-color: #000;
  border-radius: 3px;
}

.phone-notch {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 180px;
  height: 30px;
  background-color: #1a1a1a;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  z-index: 10;
}

.phone-button {
  position: absolute;
  right: -5px;
  top: 100px;
  width: 5px;
  height: 60px;
  background-color: #2a2a2a;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}

.phone-button:before {
  content: '';
  position: absolute;
  top: 80px;
  width: 5px;
  height: 60px;
  background-color: #2a2a2a;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}

.iframe-container {
  width: 100%;
  height: calc(100% - 60px);
  overflow: hidden;
}

iframe {
  width: 100%;
  height: 100%;
  border: none;
  overflow: auto;
}

.code-preview {
  padding: 0;
  overflow-y: auto;
  background-color: white;
  overflow: hidden;
  width: 100%;
  height: 624px;
  border: none;  /* 这行会移除边框 */
  overflow: hidden;  /* 防止出现滚动条 */
  -webkit-overflow-scrolling: touch;
}